  flex-wrap: wrap;
}

.list-boxs .card {
  width: 32%;
  text-align: center;
  position: relative;
  max-width: 300px;
  border: 2px solid rgb(180, 180, 180);
  
}

.list-boxs .card .head,
.list-boxs .card .body {
  background-color: #FFF;
  padding: 1.5rem 1rem;
}

.list-boxs .card .head {
  color: #0e0c61;
  
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  transform: translateY(30px);
  transition: transform .3s linear;
  border-bottom: 1px solid #F2F2F2;
}
.list-boxs .card:hover .head {
  transform: translateY(0);
  box-shadow: 0 -5px 10px rgba(0, 0, 0, .1);
}
.list-boxs .starter .head{
  color: rgb(190, 190, 53);
}
.list-boxs .standard .head{
  color: rgb(19, 212, 19);
}

.list-boxs .premium .head{
  color: rgb(60, 48, 216);
}

