.toggleswitch {
   margin-left: 10px;
   margin-right: 10px;
  
}
.toggleswitch input[type="checkbox"] {
  position: relative;
  width: 55px;
  height: 30px;
  -webkit-appearance: none;
  appearance: none;
  background: rgb(44, 62, 224);
  outline: none;
  border-radius: 2rem;
  cursor: pointer;

  box-shadow: inset 0 0 5px rgb(0 0 0 / 50%);
}
.toggleswitch [type="checkbox"]::before {
  content: "";
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
   margin: 2px;
  transition: 0.5s;
 
}

.toggleswitch input[type="checkbox"]:checked::before {
  transform: translateX(100%);
  background: #fff;
}

.toggleswitch [type="checkbox"]:checked {
  background: #275e3e;
}


