/* Layout */
.s-layout {
  display: flex;
  width: 100%;
  min-height: 100vh;
}

.s-layout__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

/* Sidebar */
.s-sidebar__trigger {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4em;
  
}
.csvbuttoncontainer select{
  margin: 5px;
  height: 40px;
  outline: 2px solid rgb(36, 40, 233);
  border-radius: 10px;  
 
}
.dropdownwall{
  outline: 1px solid rgb(36, 40, 233);
  height: 40px;
  border-radius: 10px;
